import logo from './logo.png';
import './App.css';
import'./style.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container,Navbar,InputGroup,FormControl,Button,Nav,NavDropdown } from 'react-bootstrap';
import Prodcuts from './products';
import {useState,useEffect} from "react"


function App() {


  const [dataProdcuts,setdataProdcuts]= useState(null)
  const [isPending,setIsPending] = useState(false)

  const [search,setSearch]=useState(null)




  useEffect(()=>{

    fetch("https://rationalmd.pythonanywhere.com/prods/")
    .then(res=>{return res.json() })
    .then(data =>{ /*console.log(JSON.parse(data));*/ setdataProdcuts(data)})



  },[])



  const handleSubmit= (e)=>{ 
    e.preventDefault();

    fetch('https://rationalmd.pythonanywhere.com/search/'+search, {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
      },
  
    }).then((res) => {

      setIsPending(true)
      return res.json()

    

       
        
    
    
    
    
    }).then(data =>{ console.log((data)); setdataProdcuts(data) ; setIsPending(false)})

    window.location.href = "#prods";



  
  }

 
  return (

    <div className="App">

<Navbar bg="dark" variant="dark">
    <Container fluid>
      <Navbar.Brand href="#home" className="titre">
        <img
          alt=""
          src={logo}
          width="50"
          height="50"
          className="d-inline-block align-top "
          style={{marginRight:"5%"}}
        />{' '}
      WeedCompare
      </Navbar.Brand>
      
        
        <form className="d-flex" onSubmit={handleSubmit}>
      <input onChange={(e) =>{setSearch(e.target.value)}} className="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search"></input>
      <button className="btn btn-outline-success my-2 my-sm-0" >Search</button>
    </form>
    </Container>
  </Navbar>
      <header className="">
      <div className=".container-fluid bg-overlay">
        <section className="fog">
          <div className="absolute-bg"></div>
          
          <div className="fog-container">
            <div className="fog-img fog-img-first"></div>
            <div className="fog-img fog-img-second"></div>

          </div>
      
          <div style={ {justifyContent: "center",
          alignItems: "center"}} className="title ">
            <h1>Welcome to <br/></h1>
            <h1 style={{fontFamily:"StruckBase"}}>WeedCompare</h1>
          </div>
        </section>


       
   
      
      </div>
    
        

      </header>

      {isPending && <div>loading...</div>}



       { !isPending && dataProdcuts && <Prodcuts  dataProdcuts={dataProdcuts}/>}



        <footer>
          Mounir
        </footer>
 


      
    </div>
    
  );
}

export default App;
