import React from 'react';

const Pagination = ({ postsPerPage, totalPosts, paginate,currentPage }) => {
  const pageNumbers = [];
 

  for (let i = 1; i <= Math.ceil(totalPosts / postsPerPage); i++) {
    pageNumbers.push(i);
  }


  const activeOrNot= (num)=>{if (num==currentPage){
    return "page-item active"
  }
else{return 'pag-item'}}


  return (
    <nav>
   
{/*  general case show all pages  */}
      {  pageNumbers.length<=10 &&    <ul className='pagination justify-content-center m-1'>
   
     
        {pageNumbers.map(number => (
          <li key={number} className={activeOrNot(number)}>
            <a onClick={() => paginate(number)} href='#prods' className='page-link'>
              {number}
            </a>
          </li>
        ))}



       

      </ul>}
      





      {/*  usual cases for middle */}
      {  pageNumbers.length-currentPage>3 && currentPage>=7 &&  pageNumbers.length>10 &&      <ul className='pagination justify-content-center'>
      <li className="page-item ">
        <a onClick={() => paginate(1)} href='#prods' className='page-link ' >
              {'<<'}
            </a>
            </li>
    { currentPage-1 >0 &&   <li  className='page-item'>
        <a onClick={() => paginate(currentPage-1)} href='#prods' className='page-link' >
              {'<'}
            </a>
          </li>}
          { currentPage-1 == 0 &&   <li  className='page-item disabled'>
        <a onClick={() => paginate(currentPage-1)} href='#prods' className='page-link' >
              {'<'}
            </a>
          </li>}
     
        {pageNumbers.slice(currentPage-4,currentPage+3).map(number => (

          <li key={number} className={activeOrNot(number)}>
            <a onClick={() => paginate(number)} href='#prods' className='page-link'>
              {number}
            </a>
          </li>
          
        ))
        
        
        }


{ currentPage+1 <=pageNumbers.length &&    <li  className='page-item'>
        <a onClick={() => paginate(currentPage+1)} href='#prods' className='page-link' >
              {'>'}
            </a>
          </li>}
          { currentPage+1 >pageNumbers.length &&  <li  className='page-item disabled'>
        <a onClick={() => paginate(currentPage+1)} href='#prods' className='page-link' >
              {'>'}
            </a>
          </li>}
       
          <li className="page-item  ">
        <a onClick={() => paginate(pageNumbers.length)} href='#prods' className='page-link ' >
              {'>>'}
            </a>
            </li>
      </ul>}

       {/*  start cases */}
      
       {  currentPage<7 && pageNumbers.length>10 &&    <ul className='pagination justify-content-center'>
       <li className="page-item disabled  ">
        <a onClick={() => paginate(pageNumbers.length)} href='#prods' className='page-link ' >
              {'<<'}
            </a>
            </li>
    { currentPage-1 >0 &&   <li  className='page-item'>
        <a onClick={() => paginate(currentPage-1)} href='#prods' className='page-link' >
              {'<'}
            </a>
          </li>}
          { currentPage-1 == 0 &&   <li  className='page-item disabled'>
        <a onClick={() => paginate(currentPage-1)} href='#prods' className='page-link' >
              {'<'}
            </a>
          </li>}
    
        {pageNumbers.slice(0,7).map(number => (
          <li key={number} className={activeOrNot(number)}>
            <a onClick={() => paginate(number)} href='#prods' className='page-link'>
              {number}
            </a>
          </li>

        ))}


{ currentPage+1 <=pageNumbers.length &&   <li  className='page-item'>
        <a onClick={() => paginate(currentPage+1)} href='#prods' className='page-link' >
              {'>'}
            </a>
          </li>}
          { currentPage+1 >pageNumbers.length &&  <li  className='page-item disabled'>
        <a onClick={() => paginate(currentPage+1)} href='#prods' className='page-link' >
              {'>'}
            </a>
          </li>}

          <li className="page-item  ">
        <a onClick={() => paginate(pageNumbers.length)} href='#prods' className='page-link ' >
              {'>>'}
            </a>
            </li>
       

      </ul>}

       {/*  end cases */}
       {  pageNumbers.length-currentPage<=3 &&  pageNumbers.length>10 &&    <ul className='pagination justify-content-center'>
       <li className="page-item ">
        <a onClick={() => paginate(1)} href='#prods' className='page-link ' >
              {'<<'}
            </a>
            </li>
    { currentPage-1 >0 &&   <li  className='page-item'>
        <a onClick={() => paginate(currentPage-1)} href='#prods' className='page-link' >
              {'<'}
            </a>
          </li>}
          { currentPage-1 == 0 &&   <li  className='page-item disabled'>
        <a onClick={() => paginate(currentPage-1)} href='#prods' className='page-link' >
              {'<'}
            </a>
          </li>}
    
        {pageNumbers.slice(pageNumbers.length-4-3,pageNumbers.length).map(number => (
          <li key={number} className={activeOrNot(number)}>
            <a onClick={() => paginate(number)} href='#prods' className='page-link'>
              {number}
            </a>
          </li>
        ))}


{ currentPage+1 <=pageNumbers.length &&   <li  className='page-item'>
        <a onClick={() => paginate(currentPage+1)} href='#prods' className='page-link' >
              {'>'}
            </a>
          </li>}
          { currentPage+1 >pageNumbers.length &&  <li  className='page-item disabled'>
        <a onClick={() => paginate(currentPage+1)} href='#prods' className='page-link' >
              {'>'}
            </a>
          </li>}

        
        <li className="page-item disabled">
        <a onClick={() => paginate(currentPage+1)} href='#prods' className='page-link ' >
              {'>>'}
            </a>
            </li>
       

      </ul>}
      
      

      

       

    </nav>
  );
  
};

export default Pagination;